import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import moment from 'moment';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import {
  Box,
  Button,
  Card,
  Checkbox,
  Divider,
  IconButton,
  InputAdornment,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  TextField,
  Typography,
  Grid,
  makeStyles
} from '@material-ui/core';
import {
  Edit as EditIcon,
  ArrowRight as ArrowRightIcon,
  
  Search as SearchIcon
} from 'react-feather';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import {listAllClasses} from '../../../api/classes'
import { Subscriptions, TransferWithinAStation, VpnKey } from '@material-ui/icons';
import Subscription from 'src/views/account/AccountView/Subscription';

const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];

const sortOptions = [
  {
    value: 'name|asc',
    label: 'Tên học sinh (tăng)'
  },
  {
    value: 'name|desc',
    label: 'Tên học sinh (giảm)'
  }
];

const applyFilters = (students, query, filters) => {
  return students.filter((student) => {
    let matches = true;

    if (query) {
      const properties = ['username', 'name'];
      let containsQuery = false;

      properties.forEach((property) => {
        if (student[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];
      if(key==="class" && student[key]){
        if(student[key].id!==filters[key]){
           matches = false;
        }
      } else if (value && student[key] !== value) {
        matches = false;
      } if(value==""){
        matches = true;
      }
    });

    return matches;
  });
};

const applyPagination = (students, page, limit) => {
  return students.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (students, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = students.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({
  className,
  students,
  ...rest
}) => {
  let schoolid = localStorage.getItem('schoolid');
  const classes = useStyles();
  var [sclasses, setClasses] = useState([]);
  const isMountedRef = useIsMountedRef();
  const getClasses = useCallback(async () => {
    try {
      const sclasses = await listAllClasses(schoolid);
      if (isMountedRef.current) {
        setClasses(sclasses);
        console.log("class loaded")
        // setSClass(sclasses[0])
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getClasses();
  }, [getClasses]);
  const [sclass, setSClass] = useState("");
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedStudents, setSelectedStudents] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedStudents([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };
  const handleClassChange = (event) => {
    event.persist();
    setSClass(event.target.value);
    console.log(event.target.value)
    setFilters({"class":event.target.value});
  };

  const handleSelectAllStudents = (event) => {
    setSelectedStudents(event.target.checked
      ? students.map((student) => student.id)
      : []);
  };

  const handleSelectOneStudent = (event, studentId) => {
    if (!selectedStudents.includes(studentId)) {
      setSelectedStudents((prevSelected) => [...prevSelected, studentId]);
    } else {
      setSelectedStudents((prevSelected) => prevSelected.filter((id) => id !== studentId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const filteredClasses = applyFilters(students, query, filters);
  const sortedClasses = applySort(filteredClasses, sort);
  const paginatedStudents = applyPagination(sortedClasses, page, limit);
  const enableBulkOperations = selectedStudents.length > 0;
  const selectedSomeClasses = selectedStudents.length > 0 && selectedStudents.length < students.length;
  const selectedAllClasses = selectedStudents.length === students.length;
  sclasses = applySort(sclasses, "name|asc");

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Tabs
        onChange={handleTabsChange}
        scrollButtons="auto"
        textColor="secondary"
        value={currentTab}
        variant="scrollable"
      >
        {tabs.map((tab) => (
          <Tab
            key={tab.value}
            value={tab.value}
            label={tab.label}
          />
        ))}
      </Tabs>
      <Divider />
      <Box
        p={2}
        minHeight={56}
        display="flex"
        alignItems="center"
      >

     
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon
                  fontSize="small"
                  color="action"
                >
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange}
          placeholder="Tìm kiếm theo tên học sinh"
          value={query}
          variant="outlined"
        />
        <TextField
          label="Lớp"
          name="sclass"
          value={sclass}
          onChange={handleClassChange}
          select
          SelectProps={{ native: true }}
          variant="outlined"
        >
          <option></option>
          {sclasses.map((s) => (

            <option
              key={s.id}
              value={s.id}
            >
              {s.name}
            </option>
          ))}
        </TextField>
        <Box flexGrow={1} />
        <TextField
          label="Sắp xếp"
          name="sort"
          onChange={handleSortChange}
          select
          SelectProps={{ native: true }}
          value={sort}
          variant="outlined"
        >
          {sortOptions.map((option) => (
            <option
              key={option.value}
              value={option.value}
            >
              {option.label}
            </option>
          ))}
        </TextField>
      </Box>
     
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox
              checked={selectedAllClasses}
              indeterminate={selectedSomeClasses}
              onChange={handleSelectAllStudents}
            />
            <Button
              variant="outlined"
              className={classes.bulkAction}
            >
              Xóa
            </Button>
            <Button
              variant="outlined"
              className={classes.bulkAction}
            >
              Chuyển lớp
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllClasses}
                    indeterminate={selectedSomeClasses}
                    onChange={handleSelectAllStudents}
                  />
                </TableCell>
                <TableCell>
                  Tên học sinh
                </TableCell>
                <TableCell>
                  Tên đăng nhập
                </TableCell>
                <TableCell>
                  Giới tính
                </TableCell>
                <TableCell>
                  Ngày sinh 
                </TableCell>
                <TableCell>
                  Tên cha mẹ
                </TableCell>
                <TableCell>
                  Số điện thoại
                </TableCell>

                <TableCell>
                  Trạng thái
                </TableCell>
                <TableCell align="center">
                  Lệnh xử lý
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedStudents.map((student) => {
                let birthdate = null
                if(student.birthdate){
                  birthdate = student.birthdate.seconds*1000;
                }
                
                const isStudentSelected = selectedStudents.includes(student.id);

                return (
                  <TableRow
                    hover
                    key={student.id}
                    selected={isStudentSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isStudentSelected}
                        onChange={(event) => handleSelectOneStudent(event, student.id)}
                        value={isStudentSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Box
                        display="flex"
                        alignItems="center"
                      >
                            
                          
                          <Typography
                          >
                            {student.name}
                          </Typography>
                        
                      </Box>
                    </TableCell>
                  <TableCell>
                  {student.username}
                  </TableCell>
                    <TableCell>
                    {student.gender}
                    </TableCell>
                    <TableCell>
                    {moment(birthdate||null).format('DD/MM/YYYY')}
                    </TableCell>
                    <TableCell>
                    {student.parent_name}
                    </TableCell>

                    <TableCell>
                    {student.parent_phone}
                    </TableCell>
                    <TableCell>
                    {student.status==0?"Active":"Close"}
                    </TableCell>
                    <TableCell align="right">
                      <IconButton
                        component={RouterLink}
                        to={"/school/management/students/edit/"+ student.id}
                      >
                        <SvgIcon fontSize="small">
                          <EditIcon />
                        </SvgIcon>
                      </IconButton>
                      <IconButton
                        component={RouterLink}
                        to={"/school/management/bought_students/enrole/"+ student.id}
                      >
                        <SvgIcon fontSize="small">
                          <Subscriptions />
                        </SvgIcon>
                      </IconButton>
                      
                      <IconButton
                        component={RouterLink}
                        to={"/school/management/students/changepass/"+ student.id}
                      >
                        <SvgIcon fontSize="small">
                          
                          <VpnKey />
                        </SvgIcon>
                      </IconButton>

                      <IconButton
                        component={RouterLink}
                        to={"/school/management/students/changeclass/"+ student.id}
                      >
                        <SvgIcon fontSize="small">
                          
                          <TransferWithinAStation />
                        </SvgIcon>
                      </IconButton>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredClasses.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  students: PropTypes.array.isRequired
};

Results.defaultProps = {
  students: []
};

export default Results;
